import {
  P2PKH,
  type PrivateKey,
  Script,
  type Transaction,
  TransactionSignature,
  type UnlockingScript,
  Utils,
} from "@bsv/sdk";

export const lockPrefix =
  "2097dfd76851bf465e8f715593b217714858bbe9570ff3bd5e33840a34e20ff0262102ba79df5f8ae7604a9830f03c7933028186aede0675a16f025dc4f8be8eec0382201008ce7480da41702918d1ec8e6849ba32b4d65b1e40dc669c31a1e6306b266c0000";

export const lockSuffix =
  "";

/**
 * OrdLock class implementing ScriptTemplate.
 *
 * This class provides methods for interacting with OrdinalLock contract
 */
export class LockTemplate {
  /**
   * Creates a Lock script
   *
   * @param {string} address
   * @param {number} until - Block height when unlockable
   * @returns {LockingScript} - A P2PKH locking script.
   */
  lock(address: string, until: number): Script {
    const pkh = Utils.fromBase58Check(address).data as number[];

    return new Script()
      .writeScript(Script.fromHex(lockPrefix))
      .writeBin(pkh)
      .writeNumber(until)
      .writeScript(Script.fromHex(lockSuffix));
  }

  unlock(
    privateKey: PrivateKey,
    signOutputs: "all" | "none" | "single" = "all",
    anyoneCanPay = false,
    sourceSatoshis?: number,
    lockingScript?: Script,
  ): {
    sign: (tx: Transaction, inputIndex: number) => Promise<UnlockingScript>;
    estimateLength: (tx: Transaction, inputIndex: number) => Promise<number>;
  } {
    const unlock = {
      sign: async (tx: Transaction, inputIndex: number) => {
        let signatureScope = TransactionSignature.SIGHASH_FORKID
        if (signOutputs === 'all') {
          signatureScope |= TransactionSignature.SIGHASH_ALL
        }
        if (signOutputs === 'none') {
          signatureScope |= TransactionSignature.SIGHASH_NONE
        }
        if (signOutputs === 'single') {
          signatureScope |= TransactionSignature.SIGHASH_SINGLE
        }
        if (anyoneCanPay) {
          signatureScope |= TransactionSignature.SIGHASH_ANYONECANPAY
        }
        const input = tx.inputs[inputIndex]

        const otherInputs = tx.inputs.filter((_, index) => index !== inputIndex)

        const sourceTXID = input.sourceTXID ? input.sourceTXID : input.sourceTransaction?.id('hex')
        if (!sourceTXID) {
          throw new Error(
            'The input sourceTXID or sourceTransaction is required for transaction signing.'
          )
        }
        sourceSatoshis ||= input.sourceTransaction?.outputs[input.sourceOutputIndex].satoshis
        if (!sourceSatoshis) {
          throw new Error(
            'The sourceSatoshis or input sourceTransaction is required for transaction signing.'
          )
        }
        lockingScript ||= input.sourceTransaction?.outputs[input.sourceOutputIndex].lockingScript
        if (!lockingScript) {
          throw new Error(
            'The lockingScript or input sourceTransaction is required for transaction signing.'
          )
        }

        const preimage = TransactionSignature.format({
          sourceTXID,
          sourceOutputIndex: input.sourceOutputIndex,
          sourceSatoshis,
          transactionVersion: tx.version,
          otherInputs,
          outputs: tx.outputs,
          inputIndex,
          subscript: lockingScript,
          inputSequence: input.sequence === undefined ? 0xffffffff : input.sequence,
          lockTime: tx.lockTime,
          scope: signatureScope
        })

        const p2pkh = new P2PKH().unlock(
          privateKey,
          signOutputs,
          anyoneCanPay,
          sourceSatoshis,
          lockingScript,
        );
        return (await p2pkh.sign(tx, inputIndex)).writeBin(preimage);
      },
      estimateLength: async (tx: Transaction, inputIndex: number) => {
        return (await unlock.sign(tx, inputIndex)).toBinary().length;
      },
    };
    return unlock;
  }
}
