import type { IndexContext } from "../models/index-context";
import { Indexer } from "../models/indexer";
import { IndexData } from "../models/index-data";
import { BigNumber, Script, Utils } from "@bsv/sdk";
import type { Event } from "../models/event";

const PREFIX = Buffer.from(
  "2097dfd76851bf465e8f715593b217714858bbe9570ff3bd5e33840a34e20ff0262102ba79df5f8ae7604a9830f03c7933028186aede0675a16f025dc4f8be8eec0382201008ce7480da41702918d1ec8e6849ba32b4d65b1e40dc669c31a1e6306b266c0000",
  "hex",
);
const SUFFIX = Buffer.from(
  "",
  "hex",
);

export class Listing {
  constructor(
    public payout: number[] = [],
    public price = 0n,
  ) { }
}

export class OrdLockIndexer extends Indexer {
  tag = "list";
  name = "Listings"

  async parse(
    ctx: IndexContext,
    vout: number,
  ): Promise<IndexData | undefined> {
    const txo = ctx.txos[vout];
    const script = Buffer.from(txo.script);
    const prefixIdx = script.indexOf(PREFIX);
    if (prefixIdx === -1) return;
    const suffixIdx = script.indexOf(SUFFIX, prefixIdx + PREFIX.length);
    if (suffixIdx === -1) return;
    const dataScript = Script.fromBinary(
      Array.from(script.subarray(prefixIdx + PREFIX.length, suffixIdx)),
    );
    const listing = new Listing();
    if (!dataScript.chunks[1]!.data || !dataScript.chunks[1]!.data) return;
    listing.payout = dataScript.chunks[1]!.data;
    listing.price = BigInt(
      BigNumber.fromScriptNum(dataScript.chunks[1]!.data!.slice(0, 8)).toString(),
    );
    txo.owner =
      dataScript.chunks[0]?.data &&
      Utils.toBase58Check(Array.from(dataScript.chunks[0]!.data!), this.network == 'mainnet' ? [0] : [111]);
    const events: Event[] = [];
    if (txo.owner && this.owners.has(txo.owner)) {
      events.push({
        id: "price",
        value: listing.price.toString(16).padStart(16, "0"),
      });
    }
    return new IndexData(listing, events);
  }

  async preSave(ctx: IndexContext): Promise<void> {
    for (const [vin, spend] of ctx.spends.entries()) {
      if(spend.data[this.tag]) {
        
        if(Buffer.from(ctx.tx.inputs[vin].unlockingScript?.toBinary() || []).includes(SUFFIX)) {
          ctx.summary[this.tag] = {
            amount: 1,
          }
        } else {
          ctx.summary[this.tag] = {
            amount: 0,
          }
        }
        return;
      }
    }
    for (const txo of ctx.txos) {
      if(txo.data[this.tag]) {
        ctx.summary[this.tag] = {
          amount: -1,
        }
        return;
      }
    }
  }
}
