import { Bsv20Indexer, Bsv21Indexer, FundIndexer, IndexMode, InscriptionIndexer, LockIndexer, MapIndexer, OneSatIndexer, OneSatWebSPV, OrdLockIndexer, OriginIndexer, SigmaIndexer, } from 'spv-store';
import { NetWork } from 'yours-wallet-provider';
import { YoursEventName } from './inject';
import { sendMessage } from './utils/chromeHelpers';
import { theme } from './theme';
export const initOneSatSPV = async (chromeStorageService, startSync = false) => {
    const { selectedAccount, account } = chromeStorageService.getCurrentAccountObject();
    const network = chromeStorageService.getNetwork();
    let { bsvAddress, identityAddress, ordAddress } = account?.addresses || {};
    if (!bsvAddress)
        bsvAddress = '';
    if (!identityAddress)
        identityAddress = '';
    if (!ordAddress)
        ordAddress = '';
    const owners = new Set([bsvAddress, identityAddress, ordAddress]);
    const indexers = [new FundIndexer(owners, IndexMode.TrustAndVerify, network)];
    const lockIndexer = new LockIndexer(owners, IndexMode.TrustAndVerify, network);
    const bsv20Indexers = [
        new Bsv21Indexer(owners, IndexMode.Trust, network),
        new Bsv20Indexer(owners, IndexMode.Trust, network),
    ];
    const ordIndexers = [
        new OneSatIndexer(owners, IndexMode.TrustAndVerify, network),
        new OrdLockIndexer(owners, IndexMode.TrustAndVerify, network),
        new InscriptionIndexer(owners, IndexMode.TrustAndVerify, network),
        new MapIndexer(owners, IndexMode.Verify, network),
        new SigmaIndexer(owners, IndexMode.Verify, network),
        new OriginIndexer(owners, IndexMode.TrustAndVerify, network),
    ];
    if (theme.settings.services.locks)
        indexers.push(lockIndexer);
    if (theme.settings.services.ordinals)
        indexers.push(...ordIndexers);
    if (theme.settings.services.bsv20)
        indexers.push(...bsv20Indexers);
    const oneSatSPV = await OneSatWebSPV.init(selectedAccount || '', indexers, owners, startSync && !!account, network == NetWork.Mainnet ? NetWork.Mainnet : NetWork.Testnet);
    if (!oneSatSPV)
        throw Error('SPV not initialized!');
    await registerEventListeners(oneSatSPV, selectedAccount || '', startSync);
    return oneSatSPV;
};
const registerEventListeners = async (oneSatSPV, selectedAccount, startSync) => {
    oneSatSPV.events.on('queueStats', (data) => {
        const message = { action: YoursEventName.QUEUE_STATUS_UPDATE, data };
        try {
            sendMessage(message);
            // eslint-disable-next-line no-empty
        }
        catch (e) { }
    });
    oneSatSPV.events.on('importing', (data) => {
        const message = { action: YoursEventName.IMPORT_STATUS_UPDATE, data };
        try {
            sendMessage(message);
            // eslint-disable-next-line no-empty
        }
        catch (e) { }
    });
    oneSatSPV.events.on('fetchingTx', (data) => {
        const message = { action: YoursEventName.FETCHING_TX_STATUS_UPDATE, data };
        try {
            sendMessage(message);
            // eslint-disable-next-line no-empty
        }
        catch (e) { }
    });
    if (startSync) {
        const tip = await oneSatSPV.getChaintip();
        oneSatSPV.events.on('syncedBlockHeight', (lastHeight) => {
            try {
                const message = {
                    action: YoursEventName.BLOCK_HEIGHT_UPDATE,
                    data: { currentHeight: tip?.height || 0, lastHeight },
                };
                selectedAccount && sendMessage(message);
                // eslint-disable-next-line no-empty
            }
            catch (error) { }
        });
    }
};
