import axios from 'axios';
import { NetWork } from 'yours-wallet-provider';
import { GP_BASE_URL, GP_TESTNET_BASE_URL } from '../utils/constants';
import { isBSV20v2 } from '../utils/ordi';
export class GorillaPoolService {
    chromeStorageService;
    constructor(chromeStorageService) {
        this.chromeStorageService = chromeStorageService;
    }
    getBaseUrl(network) {
        return network === NetWork.Mainnet ? GP_BASE_URL : GP_TESTNET_BASE_URL;
    }
    getUtxoByOutpoint = async (outpoint) => {
        try {
            const network = this.chromeStorageService.getNetwork();
            const { data } = await axios.get(`${this.getBaseUrl(network)}/api/txos/${outpoint}?script=true`);
            const ordUtxo = data;
            if (!ordUtxo.script)
                throw Error('No script when fetching by outpoint');
            return ordUtxo;
        }
        catch (e) {
            throw new Error(JSON.stringify(e));
        }
    };
    getTokenPriceInSats = async (tokenIds) => {
        const network = this.chromeStorageService.getNetwork();
        const result = [];
        for (const tokenId of tokenIds) {
            const { data } = await axios.get(`${this.getBaseUrl(network)}/api/bsv20/market?sort=price_per_token&dir=asc&limit=1&offset=0&${tokenId.length > 30 ? 'id' : 'tick'}=${tokenId}`);
            if (data.length > 0) {
                result.push({ id: tokenId, satPrice: data[0].pricePer });
            }
        }
        return result;
    };
    getBsv20Balances = async (addresses) => {
        const network = this.chromeStorageService.getNetwork();
        const url = `${this.getBaseUrl(network)}/api/bsv20/balance?addresses=${addresses.join('&addresses=')}`;
        const res = await axios.get(url);
        const bsv20List = res.data.map((b) => {
            const id = (b.tick || b.id);
            return {
                id: id,
                tick: b.tick,
                sym: b.sym || null,
                icon: b.icon || null,
                dec: b.dec,
                all: {
                    confirmed: BigInt(b.all.confirmed),
                    pending: BigInt(b.all.pending),
                },
                listed: {
                    confirmed: BigInt(b.all.confirmed),
                    pending: BigInt(b.all.pending),
                },
            };
        });
        return bsv20List;
    };
    getBSV20Utxos = async (tick, addresses = []) => {
        try {
            const network = this.chromeStorageService.getNetwork();
            const utxos = [];
            await Promise.all(addresses.map(async (address) => {
                const url = isBSV20v2(tick)
                    ? `${this.getBaseUrl(network)}/api/bsv20/${address}/id/${tick}?limit=10000`
                    : `${this.getBaseUrl(network)}/api/bsv20/${address}/tick/${tick}?limit=10000`;
                const r = await axios.get(url);
                r.data.forEach((utxo) => {
                    if (utxo.status === 1 && !utxo.listing)
                        utxos.push(utxo);
                });
            }));
            return utxos;
        }
        catch (error) {
            console.error('getBSV20Utxos', error);
            return [];
        }
    };
    getBsv20Details = async (tick) => {
        try {
            const network = this.chromeStorageService.getNetwork();
            const url = isBSV20v2(tick)
                ? `${this.getBaseUrl(network)}/api/bsv20/id/${tick}`
                : `${this.getBaseUrl(network)}/api/bsv20/tick/${tick}`;
            const r = await axios.get(url);
            return r.data;
        }
        catch (error) {
            console.error('getBsv20Details', error);
        }
    };
}
